*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --font-family: "Montserrat", sans-serif;
  --main-body-color: #000;
  --card-body-color: #fff2fd;
  --body-decoration-el-color: #212121;
  --card-button-color: #4c00fe;
  --field-background-color: rgba(255, 255, 255, .2);
  --header-gradient-color: rgba(11, 11, 11, .5);
  --main-pulsar-color: #ff00de;
  --main-text-color: #fff;
  --secondary-text-color: #939393;
  --hero-text-color: #dc56c5;
  --card-paragraph-text: #0e0e0e;
  --rose-border-color: rgba(220, 86, 197, .3);
  --timing-function: 250ms cubic-bezier(.4, 0, .2, 1);
}

.circle {
  width: 200px;
  height: 180px;
  fill: linear-gradient(136deg, #c4c4c4 0%, rgba(196, 196, 196, 0) 100%);
  backdrop-filter: blur(30px);
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-50%, -50%);
}

#dropdown-input, #search-box {
  backdrop-filter: blur(20px);
  color: #939393;
  height: 40px;
  box-sizing: border-box;
  cursor: pointer;
  background-color: rgba(255, 255, 255, .2);
  border: 1px solid #ccc;
  border-radius: 20px;
  font-family: Montserrat, sans-serif;
  font-size: 16px;
  font-weight: 400;
}

