  font-weight: 400;
}

#country-box::-webkit-input-placeholder, #search-box::-webkit-input-placeholder, #dropdown-input::-webkit-placeholder {
  color: #939393;
  font-size: 16px;
  font-weight: 400;
}

#country-box::-moz-placeholder, #search-box::-moz-placeholder, #dropdown-input::-moz-placeholder {
