  font-size: 16px;
  font-weight: 400;
}

#country-box::-moz-placeholder, #search-box::-moz-placeholder, #dropdown-input::-ms-placeholder {
  color: #939393;
  font-size: 16px;
  font-weight: 400;
}

.dropdown-icon {
  pointer-events: none;
  position: absolute;
  top: 50%;
  right: -55px;
  transform: translateY(-50%);
}

.dropdown-menu li {
