.card {
  height: 320px;
  width: 199px;
  cursor: pointer;
  z-index: 1;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.cards {
  flex-wrap: wrap;
  justify-content: center;
  gap: 16px;
  display: flex;
}

.card:hover {
  background: rgba(220, 86, 197, .3);
  border: 1px solid rgba(220, 86, 197, .3);
  border-top-left-radius: 50px;
  border-bottom-right-radius: 50px;
}

.event-image img {
  height: 120px;
  width: 120px;
  border-radius: 50px 0;
  position: absolute;
  top: 20px;
  right: 20px;
}

.event-image {
  height: 120px;
  width: 120px;
  border: 1px solid rgba(220, 86, 197, .3);
  border-top-left-radius: 50px;
  border-bottom-right-radius: 50px;
  position: relative;
}

.events {
  text-align: center;
  flex-direction: column;
  display: flex;
  position: relative;
}

.event-name {
  color: #dc56c5;
  size: 16px;
  flex-wrap: wrap;
  margin-top: 30px;
  margin-bottom: 3px;
  font-weight: 700;
  display: flex;
}

.event-date {
  color: #fff;
  text-align: center;
  size: 16px;
  margin: 3px;
  font-weight: 400;
}

.event-place {
  color: #fff;
  text-align: center;
  size: 14px;
  margin: 3px;
  font-weight: 600;
}

.event-place:before {
  content: url("location.3a4deced.svg");
  width: 24px;
  height: 17px;
  gap: 10px;
  margin-right: 10px;
}

@media screen and (min-width: 768px) {
  .event-image img, .event-image {
    width: 141px;
    height: 120px;
