.modal {
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: rgba(0, 0, 0, .5);
  justify-content: center;
  align-items: center;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

#modal-body {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 80px 0 7px;
  display: flex;
}

.modal-content {
  width: 280px;
  height: auto;
  opacity: 0px;
  background: #fff2fd;
  border: 1px solid #888;
  border-radius: 150px 0;
  gap: 0;
  padding: 20px;
  position: relative;
  top: 400px;
  left: 20px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, .3);
}

.close {
  color: #4c00fe;
  cursor: pointer;
  font-size: 28px;
  font-weight: bold;
  position: absolute;
  top: 10px;
  right: 10px;
}

.close:hover, .close:focus {
  color: #000;
  text-decoration: none;
}

.modal-logo {
  width: 102px;
  height: 102px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 70px;
  margin-bottom: 20px;
}

.modal-image {
  display: none;
}

.modal-title {
  font-family: var(--font-family);
  color: #dc56c5;
  text-align: center;
  text-transform: uppercase;
  margin: 0 0 7px;
  font-size: 20px;
  font-weight: 600;
}

.modal-text {
  font-family: var(--font-family);
  color: #0e0e0e;
  text-align: center;
  width: 100%;
  text-overflow: ellipsis;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  margin: 0 0 16px;
  font-size: 16px;
  font-weight: 400;
  display: -webkit-box;
  overflow: hidden;
}

.modal-main {
  flex-direction: column;
  display: flex;
}

.modal-main-text {
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.modal-buy {
  flex-direction: column;
  align-self: start;
  margin-top: 20px;
  display: flex;
}

.price {
  text-transform: capitalize;
  align-items: center;
  display: flex;
}

.price:before {
  content: url("barcodeSvg.299cbbb3.svg");
  width: 24px;
  height: 17px;
  gap: 10px;
  margin-right: 10px;
}

.button-grup {
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  margin-bottom: 20px;
  display: flex;
}

.button-standard, .button-vip {
  width: 100%;
  max-width: 120px;
  color: #fff;
  cursor: pointer;
  box-sizing: border-box;
  text-align: center;
  white-space: nowrap;
  text-transform: ellipsis;
  background-color: #4c00fe;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 14px;
  transition: background-color .3s;
  overflow: hidden;
}

.button-standard:hover, .button-vip:hover {
  background-color: #2e009a;
}

.button-more {
  max-width: 200px;
  color: #dc56c5;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #4c00fe;
  border-radius: 5px;
  margin-top: 15px;
  margin-bottom: 30px;
  margin-left: 10%;
  font-size: 12px;
  line-height: 19.5px;
  display: inline-block;
}

.button-more:hover {
  color: #fff;
  background-color: #dc56c5;
}

